.item {
    min-height: 30rem;
    min-width: 25rem;
    padding: 40px;
}

.item img {
    width: 100%;
    height: 100%;
    border-radius: 2rem;
    pointer-events: none;
}

.inner-carousel {
    display: flex;
}

.carousel {
    cursor: grab;
    overflow: hidden;
    box-shadow: 0 2px 3px;
    border-radius: 2rem;
    background-color: #efefef;
}